export default{
   data() {
       return{
           searchValue:"",
           /* 历史搜索记录 */
           history:[
               "haode",
               "shima"
           ],
       }
   },
   name:"history",
   methods:{
       routerback(){
           this.$router.goBack();
       },
       /* 搜索函数 */
       submit(){
           this.searchValue=this.searchValue.trim();
           if(!this.searchValue){
               return false;
           }
           for(let item of this.history) {
               if(item==this.searchValue){
                    this.searchValue="";
                   return false;
               }
           }
           this.history.push(this.searchValue);
           localStorage.setItem("zj_history",JSON.stringify(this.history));
           window.location.href="/#/goodslist?goods_name="+this.searchValue;
           this.searchValue="";         
       },
       /* 历史记录搜索 */
       historySearch(item){
           this.searchValue=item;
           window.location.href="/#/goodslist/"+this.searchValue;
           this.searchValue="";  
       },
       /* 清空历史记录 */
       cleanHistory(){
          this.history=[];
           localStorage.setItem("zj_history",JSON.stringify(this.history)); 
       }
   },
   mounted(){
      /* 将搜索历史记录到本地存储中 */ 
        let history=JSON.parse(localStorage.getItem('zj_history'));
        if(!history){
            history=[]; 
        }
        /* 搜索历史赋值 */
        this.history=history;
        console.log(this.$route.path);
   }
}  



